// src/pages/AnalysisPage.tsx
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useFileManager } from "../hooks/useFileManager";
import { getPlugins, AnalysisPlugin } from "../analysis/registry";
import MultiSelectModal from "../components/MultiSelectModal";
import { useUserContext } from "../context/UserContext";
import { useAnalysisManager } from "../hooks/useAnalysisManager";

export enum Status {
  Success = "",
  Failure = "",
}

interface PluginGroup {
  name: string;
  plugins: AnalysisPlugin[];
}

const AnalysisPage: React.FC = () => {
  const { files } = useFileManager();
  const { users } = useUserContext();
  const { history, removeHistoryRecord, updateHistoryRecord } =
    useAnalysisManager();

  const [searchTerm, setSearchTerm] = useState<string>("");
  const [filterAnalysisIds, setFilterAnalysisIds] = useState<string[]>([]);
  const [filterUserIds, setFilterUserIds] = useState<string[]>([]);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [showHistory, setShowHistory] = useState(false);
  const itemsPerPage = 10;

  const plugins = getPlugins();

  // 
  const groupedPlugins = plugins.reduce<PluginGroup[]>((acc, plugin) => {
    const groupName = plugin.group || '';
    const existingGroup = acc.find((g) => g.name === groupName);
    if (existingGroup) {
      existingGroup.plugins.push(plugin);
    } else {
      acc.push({ name: groupName, plugins: [plugin] });
    }
    return acc;
  }, []);

  groupedPlugins.sort((a, b) => {
    if (a.name === '') return -1;
    if (b.name === '') return 1;
    return a.name.localeCompare(b.name);
  });

  useEffect(() => {
    if (filterAnalysisIds.length === 0) {
      setFilterAnalysisIds(getPlugins().map((p) => p.id));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (filterUserIds.length === 0 && users.length > 0) {
      setFilterUserIds(users.map((u) => u.id));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [users]);

  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm, filterAnalysisIds, filterUserIds, history]);

  const filteredHistory = history.filter((record) => {
    if (
      filterAnalysisIds.length > 0 &&
      !filterAnalysisIds.includes(record.analysisId)
    ) {
      return false;
    }
    if (filterUserIds.length > 0 && !filterUserIds.includes(record.userId)) {
      return false;
    }
    if (!searchTerm) return true;
    const userForRecord = users.find((u) => u.id === record.userId);
    const fileNames = record.selectedFileIds
      .map((id) => {
        const f = files.find((fl) => fl.id === id);
        return f?.alias || f?.fileName || "";
      })
      .join(" ");
    const target =
      `${record.analysisName} ${userForRecord?.name || ""} ${fileNames} ${record.description || ""}`.toLowerCase();
    return target.includes(searchTerm.toLowerCase());
  });

  const totalPages = Math.ceil(filteredHistory.length / itemsPerPage);
  const paginatedHistory = filteredHistory.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage,
  );

  return (
    <div className="space-y-12">
      {/* Hero Section */}
      <div className="text-center py-6">
        <h1 className="text-3xl font-light text-stone-800 mb-3 tracking-wide">
          
        </h1>
        <p className="text-stone-500 max-w-lg mx-auto">
          
        </p>
      </div>

      {/*  */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Link
          to="/files"
          className="group flex items-center gap-4 p-5 bg-white rounded-xl border border-stone-200 hover:border-stone-300 transition-all duration-300"
        >
          <div className="w-10 h-10 rounded-full bg-stone-100 flex items-center justify-center text-stone-600 group-hover:bg-stone-200 transition-colors">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
            </svg>
          </div>
          <div>
            <h3 className="font-medium text-stone-800"></h3>
            <p className="text-sm text-stone-400"></p>
          </div>
        </Link>

        <Link
          to="/users"
          className="group flex items-center gap-4 p-5 bg-white rounded-xl border border-stone-200 hover:border-stone-300 transition-all duration-300"
        >
          <div className="w-10 h-10 rounded-full bg-stone-100 flex items-center justify-center text-stone-600 group-hover:bg-stone-200 transition-colors">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
            </svg>
          </div>
          <div>
            <h3 className="font-medium text-stone-800"></h3>
            <p className="text-sm text-stone-400"></p>
          </div>
        </Link>

        <Link
          to="/tutorial"
          className="group flex items-center gap-4 p-5 bg-white rounded-xl border border-stone-200 hover:border-stone-300 transition-all duration-300"
        >
          <div className="w-10 h-10 rounded-full bg-stone-100 flex items-center justify-center text-stone-600 group-hover:bg-stone-200 transition-colors">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
            </svg>
          </div>
          <div>
            <h3 className="font-medium text-stone-800"></h3>
            <p className="text-sm text-stone-400"></p>
          </div>
        </Link>
      </div>

      {/*  */}
      <div>
        <h2 className="text-lg font-medium text-stone-700 mb-6"></h2>

        {groupedPlugins.map((group) => (
          <div key={group.name} className="mb-10">
            <div className="flex items-center gap-2 mb-4">
              <span className="text-sm text-stone-400">{group.name}</span>
              <div className="flex-1 h-px bg-stone-200"></div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
              {group.plugins.map((plugin) => (
                <Link
                  key={plugin.id}
                  to={`/analysis/${plugin.id}`}
                  className="group bg-white rounded-xl border border-stone-200 p-5 hover:border-stone-300 hover:shadow-sm transition-all duration-300"
                >
                  <div className="flex items-start justify-between mb-2">
                    <h4 className="font-medium text-stone-800 group-hover:text-stone-900">
                      {plugin.name}
                    </h4>
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-stone-300 group-hover:text-stone-500 group-hover:translate-x-0.5 transition-all" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                      <path strokeLinecap="round" strokeLinejoin="round" d="M9 5l7 7-7 7" />
                    </svg>
                  </div>
                  <p className="text-sm text-stone-500 line-clamp-2 mb-3">
                    {plugin.description}
                  </p>
                  <div className="flex items-center gap-1.5 text-xs text-stone-400">
                    <span className="w-1 h-1 rounded-full bg-stone-300"></span>
                    <span>{plugin.requiredFiles.length} </span>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        ))}
      </div>

      {/*  */}
      <div className="bg-white rounded-xl border border-stone-200">
        <button
          onClick={() => setShowHistory(!showHistory)}
          className="w-full flex items-center justify-between p-5 hover:bg-stone-50 transition-colors rounded-xl"
        >
          <div className="flex items-center gap-3">
            <div className="w-9 h-9 rounded-full bg-stone-100 flex items-center justify-center text-stone-500">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                <path strokeLinecap="round" strokeLinejoin="round" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div className="text-left">
              <h3 className="font-medium text-stone-800"></h3>
              <p className="text-sm text-stone-400">{history.length} </p>
            </div>
          </div>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className={`h-4 w-4 text-stone-400 transition-transform duration-300 ${showHistory ? 'rotate-180' : ''}`}
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={2}
          >
            <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
          </svg>
        </button>

        {showHistory && (
          <div className="border-t border-stone-100">
            {/*  */}
            <div className="p-4 bg-stone-50/50 border-b border-stone-100">
              <div className="flex flex-col md:flex-row gap-3">
                <div className="flex-1">
                  <input
                    type="text"
                    className="w-full px-3 py-2 bg-white border border-stone-200 rounded-lg text-sm focus:outline-none focus:border-stone-400 transition-colors placeholder:text-stone-400"
                    placeholder="..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                  />
                </div>
                <MultiSelectModal
                  options={getPlugins().map((p) => ({ value: p.id, label: p.name }))}
                  selected={filterAnalysisIds}
                  onChange={setFilterAnalysisIds}
                  placeholder=""
                  title=""
                />
                <MultiSelectModal
                  options={users.map((u) => ({ value: u.id, label: u.name }))}
                  selected={filterUserIds}
                  onChange={setFilterUserIds}
                  placeholder=""
                  title=""
                />
              </div>
            </div>

            {/*  */}
            {filteredHistory.length === 0 ? (
              <div className="p-12 text-center">
                <div className="w-12 h-12 rounded-full bg-stone-100 flex items-center justify-center mx-auto mb-4">
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-stone-400" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                  </svg>
                </div>
                <p className="text-stone-600"></p>
                <p className="text-sm text-stone-400 mt-1"></p>
              </div>
            ) : (
              <>
                <div className="divide-y divide-stone-100">
                  {paginatedHistory.map((record) => {
                    const userForRecord = users.find((u) => u.id === record.userId);
                    return (
                      <div key={record.id} className="p-4 hover:bg-stone-50/50 transition-colors">
                        <div className="flex items-start justify-between gap-4">
                          <div className="flex-1 min-w-0">
                            <div className="flex items-center gap-2 mb-1">
                              <Link
                                to={`/analysis/${record.analysisId}`}
                                className="font-medium text-stone-800 hover:text-stone-600 transition-colors"
                              >
                                {record.analysisName}
                              </Link>
                              {record.status === Status.Success ? (
                                <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs bg-stone-100 text-stone-600">
                                  
                                </span>
                              ) : (
                                <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs bg-red-50 text-red-600">
                                  
                                </span>
                              )}
                            </div>
                            <p className="text-sm text-stone-500 mb-2">
                              {record.description || ''}
                            </p>
                            <div className="flex flex-wrap items-center gap-3 text-xs text-stone-400">
                              <span>{userForRecord?.name || ''}</span>
                              <span></span>
                              <span>{new Date(record.timestamp).toLocaleString()}</span>
                            </div>
                            <div className="flex flex-wrap gap-1.5 mt-2">
                              {record.selectedFileIds.map((fileId, index) => {
                                const file = files.find((f) => f.id === fileId);
                                return (
                                  <Link
                                    key={index}
                                    to={`/files/${file?.id}`}
                                    className="inline-flex items-center px-2 py-1 rounded text-xs bg-stone-100 text-stone-600 hover:bg-stone-200 transition-colors"
                                  >
                                    {file?.alias || file?.fileName || ''}
                                  </Link>
                                );
                              })}
                            </div>
                          </div>
                          <div className="flex items-center gap-1">
                            {record.status === Status.Success ? (
                              <Link
                                to={`/analysis/report/${record.id}`}
                                className="px-3 py-1.5 text-sm text-stone-700 bg-stone-100 rounded-lg hover:bg-stone-200 transition-colors"
                              >
                                
                              </Link>
                            ) : (
                              <Link
                                to={`/analysis/${record.analysisId}`}
                                className="px-3 py-1.5 text-sm text-stone-600 bg-stone-100 rounded-lg hover:bg-stone-200 transition-colors"
                              >
                                
                              </Link>
                            )}
                            <button
                              onClick={() => {
                                const desc = prompt("", record.description || "");
                                if (desc !== null) {
                                  updateHistoryRecord(record.id, { ...record, description: desc });
                                }
                              }}
                              className="p-1.5 text-stone-400 hover:text-stone-600 hover:bg-stone-100 rounded-lg transition-colors"
                            >
                              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                                <path strokeLinecap="round" strokeLinejoin="round" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                              </svg>
                            </button>
                            <button
                              onClick={() => removeHistoryRecord(record.id)}
                              className="p-1.5 text-stone-400 hover:text-red-500 hover:bg-red-50 rounded-lg transition-colors"
                            >
                              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                                <path strokeLinecap="round" strokeLinejoin="round" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                              </svg>
                            </button>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>

                {/*  */}
                {totalPages > 1 && (
                  <div className="flex items-center justify-center gap-2 p-4 border-t border-stone-100">
                    <button
                      onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                      disabled={currentPage === 1}
                      className="px-3 py-1.5 text-sm text-stone-600 bg-stone-100 rounded-lg hover:bg-stone-200 disabled:opacity-40 disabled:cursor-not-allowed transition-colors"
                    >
                      
                    </button>
                    <span className="text-sm text-stone-500 px-2">
                      {currentPage} / {totalPages}
                    </span>
                    <button
                      onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                      disabled={currentPage === totalPages}
                      className="px-3 py-1.5 text-sm text-stone-600 bg-stone-100 rounded-lg hover:bg-stone-200 disabled:opacity-40 disabled:cursor-not-allowed transition-colors"
                    >
                      
                    </button>
                  </div>
                )}
              </>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default AnalysisPage;
