// src/pages/FileDetailPage.tsx
import React from 'react';
import { useParams, Link } from 'react-router-dom';
import { useFileManager, UploadedFile } from '../hooks/useFileManager';
import { useUserContext } from '../context/UserContext';

const FileDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const { files } = useFileManager();
  const { users } = useUserContext();
  const fileId = Number(id);
  const file: UploadedFile | undefined = files.find((f) => f.id === fileId);

  if (!file) {
    return (
      <div className="container mx-auto">
        <div className="alert alert-error">
          <span></span>
        </div>
        <Link to="/files" className="btn btn-ghost mt-4">
          
        </Link>
      </div>
    );
  }

  const owner = users.find((u) => u.id === file.userId);

  return (
    <div className="container mx-auto">
      {/*  */}
      <div className="text-sm breadcrumbs mb-4">
        <ul>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/files"></Link>
          </li>
          <li>{file.alias || file.fileName}</li>
        </ul>
      </div>

      <h1 className="text-3xl font-bold mb-6">{file.alias || file.fileName}</h1>

      {/*  */}
      <div className="card bg-base-100 shadow-md mb-6">
        <div className="card-body">
          <h2 className="card-title"></h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
            <div>
              <p className="text-sm text-base-content/60"></p>
              <p className="font-medium">{file.fileName}</p>
            </div>
            <div>
              <p className="text-sm text-base-content/60"></p>
              <p className="font-medium">{file.alias}</p>
            </div>
            <div>
              <p className="text-sm text-base-content/60"></p>
              <p className="font-medium">
                {new Date(file.uploadedAt).toLocaleString()}
              </p>
            </div>
            <div>
              <p className="text-sm text-base-content/60"></p>
              <p className="font-medium">{owner?.name || file.userId}</p>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="card bg-base-100 shadow-md mb-6">
        <div className="card-body">
          <h2 className="card-title"></h2>
          <p className="text-sm text-base-content/60 mb-4">
             {file.data.length} 
          </p>
          <div className="overflow-x-auto max-h-96 overflow-y-auto bg-base-200 rounded-lg">
            <table className="table table-xs table-pin-rows">
              <thead>
                <tr>
                  <th>#</th>
                  {file.data[0]?.map((_, colIndex) => (
                    <th key={colIndex}> {colIndex + 1}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {file.data.slice(0, 100).map((row, rowIndex) => (
                  <tr key={rowIndex}>
                    <td className="text-base-content/60">{rowIndex + 1}</td>
                    {row.map((cell, colIndex) => (
                      <td key={colIndex}>{String(cell ?? '')}</td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
            {file.data.length > 100 && (
              <div className="text-center py-2 text-base-content/60 text-sm">
                 100  {file.data.length} 
              </div>
            )}
          </div>
        </div>
      </div>

      {/*  */}
      <div className="flex gap-2">
        <Link to="/files" className="btn btn-ghost">
          
        </Link>
      </div>
    </div>
  );
};

export default FileDetailPage;
