// src/components/ReportLayout.tsx
import React from 'react';

export interface CompanyInfo {
    name: string;
    address: string;
    id: string;
    phone: string;
    fax: string;
    logoUrl?: string;
}

export interface UserInfo {
    id: string;
    name: string;
    phone: string;
    email: string;
}

export interface TestInfo {
    testName: string;
    testTime: string;
    bluetoothSerial?: string;
    availableBase?: string;
}


export interface ReportLayoutProps {
    company: CompanyInfo;
    user: UserInfo;
    test: TestInfo;
    explanation?: string;
    // EmotionReport 
    resultContent?: React.ReactNode;
}

const ReportLayout: React.FC<ReportLayoutProps> = (
    {
        company,
        user,
        test,
        explanation,
        resultContent,
    }
) => {
    const finalLogoUrl = company.logoUrl ?? '/hylove-logo.jpg';

    return (
        <div className="card w-full bg-base-100 shadow-xl p-6">
            {/*  */}
            <h2 className="text-2xl font-bold mb-4">
                {test.testName}
            </h2>

            {/*  */}
            <div className="mb-6 flex flex-col md:flex-row items-start space-x-4">
                {/*  Logo  */}
                <div className="flex-shrink-0 w-20">
                    <img
                        src={finalLogoUrl}
                        alt="Logo"
                        className="w-full object-contain"
                    />
                </div>
                {/*  */}
                <div>
                    <h3 className="text-xl font-semibold">{company.name}</h3>
                    <p className="mt-1">{company.address}</p>
                    <p>: {company.id}</p>
                    <p>
                        Phone: {company.phone} | Fax: {company.fax}
                    </p>
                </div>
            </div>

            {/*  */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 bg-base-200 p-4 rounded">
                <div>
                    <p>
                        <strong>ID</strong> {user.id}
                    </p>
                    <p>
                        <strong></strong> {user.name}
                    </p>
                    <p>
                        <strong></strong> {user.phone}
                    </p>
                </div>
                <div>
                    <p>
                        <strong>E-Mail</strong> {user.email}
                    </p>
                    <p>
                        <strong></strong> {test.testTime}
                    </p>
                    {test.bluetoothSerial && (
                        <p>
                            <strong></strong> {test.bluetoothSerial}
                        </p>
                    )}
                    {test.availableBase && (
                        <p>
                            <strong></strong> {test.availableBase}
                        </p>
                    )}
                </div>
            </div>

            {/*  */}
            {resultContent && (
                <div className="mt-6">{resultContent}</div>
            )}

            {/*  */}
            {explanation && (
                <div className="mt-4">
                    <p className="italic text-sm text-gray-600">
                        {explanation}
                    </p>
                </div>
            )}
        </div>
    );
};

export default ReportLayout;