// src/components/UserForm.tsx
import React, {useState} from 'react';
import {User, UserRole} from "../hooks/useUserManager";

interface UserFormProps {
    initialInfo?: User;
    onSave: (info: User) => void;
    onCancel: () => void;
}

const UserForm: React.FC<UserFormProps> = ({initialInfo, onSave, onCancel}) => {
    const [form, setForm] = useState<User>(
        initialInfo || {
            id: '',
            name: '',
            phone: '',
            email: '',
            role: UserRole.Basic, // 
            company: {
                name: '',
                address: '',
                id: '',
                phone: '',
                fax: '',
            },
        }
    );

    const handleInputChange = (
        e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
    ) => {
        const {name, value} = e.target;
        if (name.startsWith('company.')) {
            const key = name.split('.')[1];
            setForm({
                ...form,
                company: {...form.company, [key]: value},
            });
        } else if (name === 'role') {
            setForm({...form, role: value as UserRole});
        } else {
            setForm({...form, [name]: value});
        }
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        //  id  id
        onSave({...form});
    };

    return (
        <form onSubmit={handleSubmit} className="space-y-4">
            <h2 className="text-2xl font-bold mb-4">{form.id ? '' : ''}</h2>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <select
                    name="role"
                    value={form.role}
                    onChange={handleInputChange}
                    className="select select-bordered"
                >
                    {Object.values(UserRole).map(role => (
                        <option key={role} value={role}>
                            {role}
                        </option>
                    ))}
                </select>
            </div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <input
                    type="text"
                    name="name"
                    value={form.name}
                    onChange={handleInputChange}
                    placeholder=""
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <input
                    type="text"
                    name="phone"
                    value={form.phone}
                    onChange={handleInputChange}
                    placeholder=""
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text">Email</span>
                </label>
                <input
                    type="email"
                    name="email"
                    value={form.email}
                    onChange={handleInputChange}
                    placeholder=" Email"
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="divider"></div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <input
                    type="text"
                    name="company.name"
                    value={form.company.name}
                    onChange={handleInputChange}
                    placeholder=""
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <input
                    type="text"
                    name="company.address"
                    value={form.company.address}
                    onChange={handleInputChange}
                    placeholder=""
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="form-control mb-4">
                <label className="label">
                    <span className="label-text"></span>
                </label>
                <input
                    type="text"
                    name="company.id"
                    value={form.company.id}
                    onChange={handleInputChange}
                    placeholder=""
                    className="input input-bordered"
                    required
                />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="form-control">
                    <label className="label">
                        <span className="label-text"></span>
                        <span className="label-text-alt text-base-content/50"></span>
                    </label>
                    <input
                        type="text"
                        name="company.phone"
                        value={form.company.phone}
                        onChange={handleInputChange}
                        placeholder=""
                        className="input input-bordered"
                    />
                </div>

                <div className="form-control">
                    <label className="label">
                        <span className="label-text"></span>
                        <span className="label-text-alt text-base-content/50"></span>
                    </label>
                    <input
                        type="text"
                        name="company.fax"
                        value={form.company.fax}
                        onChange={handleInputChange}
                        placeholder=""
                        className="input input-bordered"
                    />
                </div>
            </div>

            <div className="flex justify-end space-x-2">
                <button type="submit" className="btn btn-primary">
                    
                </button>
                <button type="button" onClick={onCancel} className="btn btn-secondary">
                    
                </button>
            </div>
        </form>
    );
};

export default UserForm;