// src/pages/UsersPage.tsx
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useUserContext } from '../context/UserContext';
import UserForm from '../components/UserForm';
import { User, UserRole } from '../hooks/useUserManager';

const UsersPage: React.FC = () => {
  const {
    users,
    currentUser,
    setCurrentUser,
    addUser,
    updateUser,
    removeUser,
  } = useUserContext();
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const [form, setForm] = useState<User | null>(null);
  const [deleteConfirmUser, setDeleteConfirmUser] = useState<User | null>(null);

  const handleAddUser = () => {
    setForm({
      id: '',
      name: '',
      phone: '',
      email: '',
      role: UserRole.Basic,
      company: {
        name: '',
        address: '',
        id: '',
        phone: '',
        fax: '',
      },
    });
    setIsModalOpen(true);
  };

  const handleEditUser = (user: User) => {
    setForm(user);
    setIsModalOpen(true);
  };

  const handleDeleteUser = (user: User) => {
    setDeleteConfirmUser(user);
  };

  const confirmDelete = () => {
    if (deleteConfirmUser) {
      removeUser(deleteConfirmUser.id);
      setDeleteConfirmUser(null);
    }
  };

  const handleSubmit = (user: User) => {
    if (!user.id) {
      addUser({ ...user, id: Date.now().toString() });
    } else {
      updateUser(user);
    }
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="container mx-auto">
      {/*  */}
      <div className="text-sm breadcrumbs mb-4">
        <ul>
          <li>
            <Link to="/"></Link>
          </li>
          <li></li>
        </ul>
      </div>

      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-6">
        <h1 className="text-3xl font-bold"></h1>
        <button className="btn btn-primary mt-4 sm:mt-0" onClick={handleAddUser}>
          
        </button>
      </div>

      {/*  */}
      {currentUser && (
        <div className="card bg-base-100 shadow-md mb-6">
          <div className="card-body">
            <div className="flex items-center justify-between">
              <h2 className="card-title"></h2>
              <span className="badge badge-primary">{currentUser.role}</span>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mt-4">
              <div>
                <p className="text-sm text-base-content/60"></p>
                <p className="font-medium">{currentUser.name}</p>
              </div>
              <div>
                <p className="text-sm text-base-content/60"></p>
                <p className="font-medium">{currentUser.phone || ''}</p>
              </div>
              <div>
                <p className="text-sm text-base-content/60">Email</p>
                <p className="font-medium">{currentUser.email || ''}</p>
              </div>
              <div>
                <p className="text-sm text-base-content/60"></p>
                <p className="font-medium">
                  {currentUser.company.name || ''}
                </p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/*  */}
      <h2 className="text-2xl font-bold mb-4"></h2>
      {users.length === 0 ? (
        <div className="text-center py-8 text-base-content/60">
          <p></p>
          <p className="text-sm mt-2"></p>
        </div>
      ) : (
        <>
          {/*  */}
          <div className="hidden md:block overflow-x-auto bg-base-100 rounded-lg shadow">
            <table className="table w-full">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>Email</th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr
                    key={user.id}
                    className={currentUser?.id === user.id ? 'bg-base-200' : ''}
                  >
                    <td>
                      <span className="badge badge-outline badge-sm">
                        {user.role}
                      </span>
                    </td>
                    <td className="font-medium">{user.name}</td>
                    <td>{user.phone || '-'}</td>
                    <td>{user.email || '-'}</td>
                    <td>{user.company.name || '-'}</td>
                    <td>
                      <div className="flex space-x-2">
                        {currentUser?.id !== user.id && (
                          <button
                            className="btn btn-sm btn-primary"
                            onClick={() => setCurrentUser(user)}
                          >
                            
                          </button>
                        )}
                        {currentUser?.id === user.id && (
                          <span className="badge badge-success"></span>
                        )}
                        <button
                          className="btn btn-sm btn-ghost"
                          onClick={() => handleEditUser(user)}
                        >
                          
                        </button>
                        <button
                          className="btn btn-sm btn-error btn-outline"
                          onClick={() => handleDeleteUser(user)}
                        >
                          
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/*  */}
          <div className="md:hidden space-y-4">
            {users.map((user) => (
              <div
                key={user.id}
                className={`card bg-base-100 shadow ${
                  currentUser?.id === user.id ? 'ring-2 ring-primary' : ''
                }`}
              >
                <div className="card-body p-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <h3 className="font-bold text-lg">{user.name}</h3>
                      <span className="badge badge-outline badge-sm">
                        {user.role}
                      </span>
                    </div>
                    {currentUser?.id === user.id && (
                      <span className="badge badge-success"></span>
                    )}
                  </div>

                  <div className="grid grid-cols-2 gap-2 mt-2 text-sm">
                    <div>
                      <p className="text-base-content/60"></p>
                      <p>{user.phone || '-'}</p>
                    </div>
                    <div>
                      <p className="text-base-content/60"></p>
                      <p>{user.company.name || '-'}</p>
                    </div>
                  </div>

                  <div className="card-actions justify-end mt-3">
                    {currentUser?.id !== user.id && (
                      <button
                        className="btn btn-sm btn-primary"
                        onClick={() => setCurrentUser(user)}
                      >
                        
                      </button>
                    )}
                    <button
                      className="btn btn-sm btn-ghost"
                      onClick={() => handleEditUser(user)}
                    >
                      
                    </button>
                    <button
                      className="btn btn-sm btn-error btn-outline"
                      onClick={() => handleDeleteUser(user)}
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </>
      )}

      {/* / Modal */}
      {isModalOpen && (
        <div className="modal modal-open">
          <div className="modal-box relative max-w-2xl">
            <button
              className="btn btn-sm btn-circle absolute right-2 top-2"
              onClick={handleCancel}
            >
              
            </button>
            <UserForm
              initialInfo={form || undefined}
              onSave={handleSubmit}
              onCancel={handleCancel}
            />
          </div>
          <div className="modal-backdrop" onClick={handleCancel} />
        </div>
      )}

      {/*  Modal */}
      {deleteConfirmUser && (
        <div className="modal modal-open">
          <div className="modal-box">
            <h3 className="font-bold text-lg"></h3>
            <p className="py-4">
              <span className="font-semibold">{deleteConfirmUser.name}</span>
              <br />
              <span className="text-sm text-base-content/60"></span>
            </p>
            <div className="modal-action">
              <button
                className="btn btn-ghost"
                onClick={() => setDeleteConfirmUser(null)}
              >
                
              </button>
              <button className="btn btn-error" onClick={confirmDelete}>
                
              </button>
            </div>
          </div>
          <div
            className="modal-backdrop"
            onClick={() => setDeleteConfirmUser(null)}
          />
        </div>
      )}
    </div>
  );
};

export default UsersPage;
