// 
export async function musicAnalysis(data: any[][]): Promise<any> {
    const payload = {
            beforeBrainData: data[0],
            afterBrainData: data[1],
    };
    try {
        return await post('/api/v1/music', payload);
    } catch (error) {
        throw error;
    }
}

