export const renderEmotionReport = (result: any): React.ReactNode => {
    const beforeScores = result.sentimentBefore.data.map((d: any) => d.score);
    const afterScores = result.sentimentAfter.data.map((d: any) => d.score);
    const categories = result.sentimentBefore.data.map((_: any, i: number) => `T ${i + 1}`);

    const options: Highcharts.Options = {
        chart: {
            type: 'column',
        },
        title: {
            text: ' Motion ',
        },
        xAxis: {
            categories,
            title: {text: ''},
        },
        yAxis: {
            min: -5,
            max: 5,
            title: {
                text: '',
            },
            plotLines: [
                {
                    value: result.sentimentBefore.sentimentAvg,
                    color: '#f87171',
                    width: 1,
                    dashStyle: 'ShortDash',
                    label: {
                        text: `: ${result.sentimentBefore.sentimentAvg}`,
                        align: 'right',
                        style: {color: '#f87171'},
                    },
                },
                {
                    value: result.sentimentAfter.sentimentAvg,
                    color: '#60a5fa',
                    width: 1,
                    dashStyle: 'ShortDash',
                    label: {
                        text: `: ${result.sentimentAfter.sentimentAvg}`,
                        align: 'right',
                        style: {color: '#60a5fa'},
                    },
                },
            ],
        },
        tooltip: {
            shared: true,
            valueSuffix: '',
        },
        plotOptions: {
            column: {
                grouping: true,
                shadow: false,
            },
        },
        series: [
            {
                name: result.sentimentBefore.name,
                data: beforeScores,
                type: 'column',
                color: '#f87171',
            },
            {
                name: result.sentimentAfter.name,
                data: afterScores,
                type: 'column',
                color: '#60a5fa',
            },
        ],
    };

    return (
        <div className="p-4">
            <h3 className="text-xl font-semibold mb-4"> Motion </h3>
            <HighchartsReact highcharts={Highcharts} options={options}/>
        </div>
    );
};

